// src/App.js
import React, { useEffect, useState } from "react";
import SearchHome from "./components/home/SearchHome";
import SearchCatalog from "./components/search/SearchCatalog";
import SearchService from "./services/SearchService";
import { sampleDatasets } from "./data/sampleDatasets";
import "./styles.css";

function App() {
  const [searchService, setSearchService] = useState(null);

  useEffect(() => {
    // Initialize the search service with sample datasets
    setSearchService(new SearchService(sampleDatasets));
  }, []);

  // Don't render until search service is initialized
  if (!searchService) {
    return <div className="loading">Loading...</div>;
  }

  return (
    <BrowserRouter>
      <div className="App">
        <header className="app-header">
          <h1>Healthcare Dataset Catalog</h1>
          <p className="app-description">
            Browse and filter medical and community health datasets from various
            sources
          </p>
        </header>

        <main id="main-content" className="app-main">
          <Routes>
            <Route path="/" element={<SearchHome searchService={searchService} />} />
            <Route path="/search" element={<SearchCatalog searchService={searchService} />} />
            <Route path="/catalog" element={<SearchCatalog searchService={searchService} />} />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </main>

        <footer className="app-footer">
          <p>
             2025 Healthcare Data Repository. All data is for research purposes
            only.
          </p>
        </footer>
      </div>
    </BrowserRouter>
  );
}

export default App;